.bottomFixedBar {
  width: 70%;
  height: 80px;
  background: white;
  position: fixed;
  bottom: 10px;
  top: auto;
  left: 0;
  right: 0;
  margin: auto;
  border-radius: 20px;
  display: flex;
}
.bottomFixedBarInner {
  display: flex;
  align-items: center;
  width: 250px;
  height: 70px;
  margin-top: 5px;
  margin-right: auto;
  margin-left: 10px;
  background: white;
  border-radius: 50px;
  background-color: black;
  margin-bottom: auto;
  text-align: center;
}
.bottomInnerTotal {
  margin-block-start: 0em;
  margin-block-end: 0em;
  color: white;
  margin-inline-start: 20px;
}
.BackButton {
  border-radius: 50px;
  width: 250px;
  right: 20px;
  margin-right: 10px;
  margin-top: auto;
  margin-bottom: auto;
  height: 70px;
  font-size: 40px;
  color: black;
  border: 1px solid;
}
